import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { ReactComponent as ExpandMoreIcon } from '../../assets/images/mobileNavbar/expandMore.svg';
import { ReactComponent as ExpandLessIcon } from '../../assets/images/mobileNavbar/expandLess.svg';

const NavLinkExpandable = ({ to, toggle, icon, text, submenu }) => {
  const [Open, setOpen] = useState(false);
  const subMenuToggle = () => setOpen(!Open);
  const ExpandableLinkToggle = () => {
    subMenuToggle();
    toggle();
  };

  return (
    <div className="flex flex-wrap items-center flex-row text-left border-solid border-gray-border border-t-0.5 lg:border-t-0 lg:w-auto py-3 px-8 sm:px-0 lg:py-0">
      <Link to={to} onClick={toggle} className="flex items-center flex-row">
        <div className="mr-4 lg:hidden">{icon}</div>
        {text}
      </Link>
      <div className="flex flex-grow justify-end lg:hidden">
        <button onClick={subMenuToggle} className="flex items-center">
          {Open ? <ExpandLessIcon /> : <ExpandMoreIcon />}
        </button>
      </div>
      <div className={Open ? 'flex flex-wrap w-full' : 'hidden'}>
        {submenu.map((item) => (
          <div className="block w-full lg:hidden">
            <ExpandableLink
              to={item.link}
              toggle={ExpandableLinkToggle}
              text={item.name}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

NavLinkExpandable.propTypes = {
  to: PropTypes.string.isRequired,
  toggle: PropTypes.func.isRequired,
  icon: PropTypes.element.isRequired,
  text: PropTypes.string.isRequired,
};

const ExpandableLink = ({ to, toggle, text }) => (
  <Link to={to} onClick={toggle} className="flex items-center flex-row">
    <div className="ml-8 pt-2 lg:hidden">{text}</div>
  </Link>
);

ExpandableLink.propTypes = {
  to: PropTypes.string.isRequired,
  toggle: PropTypes.func.isRequired,
  text: PropTypes.string.isRequired,
};

export default NavLinkExpandable;
