import React from 'react';
import PropTypes from 'prop-types';
import { ReactComponent as HamburgerIcon } from '../../assets/images/mobileNavbar/hamburger.svg';
import { ReactComponent as CloseIcon } from '../../assets/images/mobileNavbar/close.svg';

const NavMenu = ({ open, toggle }) => {
  return (
    <div className="px-8 sm:px-0 block lg:hidden">
      <button onClick={toggle} className="flex items-center">
        {open ? <CloseIcon /> : <HamburgerIcon />}
      </button>
    </div>
  );
};

NavMenu.propTypes = {
  open: PropTypes.bool.isRequired,
  toggle: PropTypes.func.isRequired,
}

export default NavMenu;